/* div에 마우스가 올라가지 않은 상태 */
.AboutMe_main{
    background: rgb(250, 237, 213);
    width: 100%;
    padding : 10px;
    border : 1px solid gray;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    overflow-y: hidden;
}

/* div에 마우스가 올라간 상태 */
.AboutMe_main_onEnter{ 
    background: rgb(250, 237, 213);
    border: 1px solid gray;
    width: 100%;
    padding : 10px;
    border : 1px solid gray;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
}
/* 스크롤 바 css ( 바 가로길이 ) */
.AboutMe_main_onEnter::-webkit-scrollbar {
    width: 10px;
}

/* 스크롤 바 css ( 바 색갈 및 모양 ) */
.AboutMe_main_onEnter::-webkit-scrollbar-thumb {
    background-color: #2f3542;
    border-radius: 10px;
}

/* 스크롤 바 css ( 바 뒷배경 )*/
 .AboutMe_main_onEnter::-webkit-scrollbar-track {
    background-color: grey;
    margin : 20px 0px ;
    border-radius: 10px;
    box-shadow: inset 0px 0px 5px white;
}

