/* education 상세 */


/* 제목 ( 프로젝트 이름 ) 상세 */
.education_header {
    text-align: center;
    /* background: red; */
    height: 10%;
}

/* education 이미지 공간 */
.education_body {
    text-align: center;
    height: 60%;
    display: flex;
    border-bottom: 1px solid black;
}

/* education 프로젝트 설명, 사용된 기술 */
.education_footer {
    /* background: green; */
    height: 30%;
    display: flex;
}

/* education 이미지 */
.education_img{
    width: 100%;
    height: 90%;
}

/* education 이미지 왼쪽 공간 */
.education_body_left {
    padding-top: 22%;
    padding-right: 10px;
    text-align: right;
    width: 20%;
}

/* education 이미지 중간 공간 */
.education_body_center {
    width: 60%;
}

/* education 이미지 오른쪽 공간 */
.education_body_right {
    padding-top: 22%;
    padding-left: 10px;
    width: 20%;
}

/* education 이미지 오른쪾 이동 버튼 */
.education_RightButton{
    width: 40px;
    height: 40px;
    border: 1px solid black;
    border-radius: 10px;
    float: left;
}

/* education 이미지 왼쪽 이동 버튼 */
.education_LefttButton{
    width: 40px;
    height: 40px;
    border: 1px solid black;
    border-radius: 10px;
    float: right;
}

/* education 프로젝트 요약 공간 */
.education_footer_left{
    padding: 20px;
    width: 50%;
    border-right: 1px solid black;
}

/* education 프로젝트 사용 기술 공간 */
.education_footer_right{
    padding: 20px;
    width: 50%;
}

